<template>
	<div class="body">
		<div class="search_group flex_between">
			<div class="flex_start">
				<div class="search_box search_box1">
					<el-input placeholder="子基金项目名称" v-model="project_name" class="" :clearable='true' @change="searchListData()" >
						<i slot="prefix" class="el-input__icon el-icon-search"></i>
					</el-input>
				</div>
			</div>
		</div>
		<div class="main under_fund">
			<div class="under_fund_header flex_between">
				<div class="under_fund_header_tname">
					子基金项目总数(&nbsp;{{total}}&nbsp;)
				</div>
				<div class="flex_end">
					<div  @click="downloadExcelData" class="table-group-title-btn btn-blue-border">
						<span class="iconfont icon-daoru"></span>导出excel表格
					</div>
					<div  @click="downloadExcelModule" class="table-group-title-btn btn-blue-border">
						<span class="iconfont icon-daoru"></span>下载子基金项目信息模版
					</div>
					<div class="">
						<label for="upload-fund-input">
							<div class="table-group-title-btn btn-blue-border">
								<span class="iconfont icon-daochu"></span>导入子基金项目信息
							</div>
						</label>
						<input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadManageCompanyInfoExcel($event)" />
					</div>
					<div class="table-group-title-btn btn-blue" @click="addMotherFund" >
						<span class="iconfont icon-jia"></span>添加新项目
					</div>
				</div>
			</div>
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%">
					<el-table-column v-for="(tableHeaderitem,index) of tableHeader " sortable  :key="index" :prop="tableHeaderitem.param" :label="tableHeaderitem.text" :width='tableHeaderitem.width'></el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<!-- <el-button @click="toSetPower(scope.$index)" type="text" size="small">设置权限</el-button>
							<el-button @click="toFundRunPage()" type="text" size="small">项目运行</el-button> -->

							<el-button @click="view(scope.row)" type="text" size="small">查看</el-button>
							<el-button @click="deleteFund(scope.row)" type="text" size="small">删除</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
				<el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="page" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total" v-if="total>0">
    			</el-pagination>
			</div>
		</div>
		<!--  设置权限弹窗开始-->
		<!-- <el-dialog title="设置可见人员" :visible.sync="isSetPowerDialogShow" width="813px" center :append-to-body='true'>
			<set-power v-on:get-selected-employee="getSelectedEmployee"></set-power>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog_btn dialog_btn_cancel" @click="isSetPowerDialogShow=false">取消</button>
				<button class="dialog_btn dialog_btn_submit" >确定</button>
			</div>
		</el-dialog> -->
		<!--设置权限弹窗结束-->
	</div>
</template>

<script>
	import setPower from '../../components/setPower.vue'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	
	import {getHavePowerUsersApi,setPowerSubmitApi} from '../../config/api'
	import {getFundList,delFundInfo,downloadadChildProjectExcel,exportExcels,addExcel} from '../../config/apiSj'
	export default {
		data() {
			return {
				isSetPowerDialogShow: false,
				project_name: '',

				//分页参数
				total:0,
				page:1,
				pageSize:10,
				
				tableHeader:[
					{
						param:'index',
						text:'序号',
						width:'80',
					},{
						param:'name',
						text:'项目名称',
						width:'150',
					},{
						param:'company_name',
						text:'企业名称',
						width:'150',
					},{
						param:'credit_code',
						text:'统一社会信用代码',
						width:'170',
					},{
						param:'quote',
						text:'上市/挂牌板块',
						width:'250',
					},{
						param:'industry',
						text:'行业',
						width:'150',
					},{
						param:'is_monitor',
						text:'是否舆情监测',
						width:'150',
					},
				],
				tableData: [],
			}
		},
		created() {
			this.getListData();
		},
		mounted() {},
		components: {
			setPower
		},
		methods: {
			//跳转新增页面
			addMotherFund(){
				this.$router.push({
					name: "subFundProjectAdd"
				})
			},
			//跳转查看页面
			view(row){
				// console.log(row);
				this.$router.push({
					name: 'subFundProjectView',
					query:{
						id:row.id,
						// name:row.name,
					}
				})
			},
			// everyPageSizeChange(e){
			// 	this.pageSize = e;
			// 	this.getSubFundList()
			// },
			//获取数据
			// changeCurrentPage(){
			// 	//assaasd
			// },
			//输入选择功能
			// querySearch(queryString, cb) {
			// 	var restaurants = this.restaurants;
			// 	var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
			// 	// 调用 callback 返回建议列表的数据
			// 	cb(results);
			// },
			// createFilter(queryString) {
			// 	return(restaurant) => {
			// 		return(restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
			// 	};
			// },
			// chooseDepartCompanyName(e){
			// },
			// toFundRunPage(){
			// 	// this.$router.push({
			// 	// 	name: "motherFundRun"
			// 	// })
			// },
			// toSetPower(){
			// 	this.isSetPowerDialogShow = true;
			// },
			// getSelectedEmployee(e){
			// 	console.log(e);
			// },


			
			//获取母基金列表信息
			getListData(){
				getFundList({
					page: this.page,
					page_size:this.pageSize,
					project_name: this.project_name,
				}).then(res => {
					if (res.data.msgNo == 1) {
						let data = res.data.payload;
						if(data.data.length>0){
							data.data.forEach((element,index) => {
								element.index = (this.page-1)*this.pageSize+index+1;
								if(element.is_monitor==1){
									element.is_monitor = '是'
								}else{
									element.is_monitor = '否'
								}
							});
						}
						this.total = data.total;
						this.tableData = data.data;
					}else{
						// dialogConfirm("info", res.data.message);
					}
				})
			},
			//搜索按钮点击事件
			// searchCompany() {
			// 	let data1 = new Date(this.BeginDate);
			// 	let data2 = new Date(this.LastDate);
			// 	if(data1.getTime() > data2.getTime()) {
			// 		dialogConfirm("info", "开始时间不能大于结束时间");
			// 		return false;
			// 	} else {
			// 		this.getListData()
			// 	}
			// },
			

			//-----
			//检索条件查询对应的数据
			searchListData(){
				this.page = 1;
				this.getListData()
			},
			//修改页面条数时调用pageSize
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getListData()
			},
			//修该当前页数时调用page
			changeCurrentPage(e){
				this.page = e;
				this.getListData()
			},

			//删除项目
			deleteFund(row){
				var message = "确定要删除该子基金项目?"
				dialogConfirmHasbtn(message).then(() => {
					delFundInfo({
							id:row.id,
						}).then(res => {
							if (res.data.msgNo == 1) {
								dialogConfirm("success", '删除成功');
								// console.log(this.tableData);
								let Listlenght = this.tableData.length;
								
								// console.log(Listlenght);
								if(Listlenght>1){
									// console.log(1111);
									this.getListData();
								}else{
									if(this.page>1){
										// console.log(2222);
										this.page = this.page - 1;
										this.getListData();
									}else{
										// console.log(3333);
										this.getListData(this.page);
									}
								}
							}else{
								dialogConfirm("info", res.data.message);
							}
						})
				}).catch(() => {});
			},
			
			
			//导入Excel文件
			uploadManageCompanyInfoExcel() {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
					dialogConfirm('info', '请选择Excel格式文件')
					inputDOM.value = '';
					return false
				}
				addExcel({},file).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '导入成功');
						this.page = 1;
						this.getListData();
					} else {
						dialogConfirm('info', res.data.message);
					}
					inputDOM.value = '';
				})
			},

			//导出excel表格
			downloadExcelData(){
				exportExcels({}).then(res => {
					if (res.data.msgNo == 1) {
						// console.log(res.data.payload)
						dialogConfirm("success", '下载成功');
						window.location.href = res.data.payload.path;
					}else{
						dialogConfirm("info", res.data.message);
					}
				})
			},

			//下载子基金项目信息模版
			downloadExcelModule(){
				// let key = window.localStorage.getItem("htcx_server_key");
				// let href = api.sourceDataUrl + "/web/Company/CompanyInfoExcel?company_id=" + this.motherFundId + '&key=' + key + '&new_type=' + 2;
				// window.location.href = href;
				downloadadChildProjectExcel().then(res => {
					if (res.data.msgNo == 1) {
						// console.log(res.data.payload)
						dialogConfirm("success", '下载成功');
						window.location.href = res.data.payload;
					}else{
						dialogConfirm("info", res.data.message);
					}
				})
			},


		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.search_group{
		padding: 20px 0px 30px 0;
		border-bottom: 1px solid #E5E5EA;
	}
	.search_box{
		width: 140px;
		height: 30px;
		margin-right: 13px;
	}
	.search_box1{
		width: 170px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	/*公司名称模块开始*/
	
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	
	/*公司名称模块结束*/
	.under_fund_header{
		margin: 30px 0px;
	}
	.under_fund_header_tname {
		/* margin: 26px 0; */
		color: #6292F4;
		font-size: 12px;
	}
	.under_fund_header_tname>div{
		color: #6292F4;
	}
	.under_fund_header_tname input {
		width: 30px;
		height: 21px;
		margin: 0 5px;
		color: #545454;
		border: 1px solid #6292F4;
		padding: 0 4px;
	}
	
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
</style>


